/**
 * 函数节流(首次执行)
 * @param {*} fn 
 * @param {*} delay 
 * @param {*} type 
 */
export const  throttle = function (fn,delay=3000,type='start'){

	let timer = null;
	let status = true;
	if(type==='start') return ()=>{
		clearTimeout(timer);
		if(status){
			status = false;
			fn.call(this,arguments);
		}
		timer = setTimeout(()=>status = true, delay);
	}
		
	else if(type==='end') return  ()=>{
		if(timer){
			clearTimeout(timer)
		}
		timer = setTimeout(()=>fn.call(this,arguments),delay)
	}
}

